  padding: 5px 0;
}

.uid-navigation a[aria-current=page] {
  color: #009de9;
}

.uid-navigation a.disabled {
  color: #999999;
}

.uid-navigation > ul > li ~ li {
  position: relative;
  padding-top: 25px;
  margin-top: 25px;
}

.uid-navigation > ul > li ~ li::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  height: 2px;
  background-color: rgba(0, 157, 233, 0.6);
}

.uid-navigation > ul > li > strong {
  font-weight: bold;
}

.uid-navigation > ul > li > .submenu {
  margin-top: 20px;
  font-size: 16px;
  text-transform: capitalize;
  font-family: "prompt";
}

.uid-navigation > ul > li > .submenu li ~ li {
  margin-top: 5px;
}

.uid-main {
  grid-area: 2/2/3/3;
}

.uid-footer {
  grid-area: 3/1/4/3;
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: 1px solid rgba(0, 0, 0, 0.3);
}

.uid-footer .copyright {
  display: block;
  text-align: center;
}

.uid-component article + article {
  margin-top: 30px;
}

.uid-component ~ .uid-component {
  margin-top: 60px;
}

.uid-title01 {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 52px;
  line-height: 1;
  color: rgba(0, 0, 0, 0.8);
}

.uid-title02 {
  margin-bottom: 15px;
  font-size: 24px;
  font-weight: 500;
}
.uid-title02 + .uid-desc {
  margin: 15px 0 15px;
}

.uid-title03 {
  margin-top: 20px;
  margin-bottom: 15px;
  font-size: 18px;
}
.uid-title03 + .uid-list {
  margin-left: 40px;
}
.uid-title03 + dl.uid-list {
  margin-left: 10px;
}

.uid-title04 {
  margin-top: 40px;
  margin-bottom: 15px;
  font-size: 18px;
}

.uid-desc {
  padding-left: 15px;
  border-left: 8px solid rgba(0, 0, 0, 0.1);
  margin: 15px 0 40px;
  font-size: 18px;
}

.uid-list {
  margin-bottom: 15px;
  margin-left: 20px;
  line-height: 1.5;
  list-style: disc;
  clear: both;
}
.uid-list.no-bullet {
  list-style: none;
  margin: 10px 0 10px 10px;
}
.uid-list.no-bullet li {
  font-size: 14px;
}
.uid-list.no-bullet li ~ li {
  margin-top: 5px;
}
.uid-list dt {
  float: left;
  clear: left;
  width: 270px; /* dt의 너비를 조정할 수 있습니다 */
  font-weight: bold;
}
.uid-list dd {
  margin: 0 0 0 290px; /* dt의 너비 + 10px 만큼 왼쪽으로 이동합니다 */
  padding: 0;
}
.uid-list + .uid-datatable {
  margin-bottom: 20px;
}
.uid-list strong {
  font-weight: bold;
}

.uid-list > li ~ li {
  margin-top: 8px;
}

.uid-caution {
  color: #cd2222;
  margin-top: 40px;
}

.uid-demo {
  position: relative;
  z-index: 5;
  padding: 15px;
  border: 5px solid rgb(243, 244, 245);
  margin-top: 25px;
}
.uid-demo .modal-btn-wrap {
  display: flex;
  justify-content: space-evenly;
}
.uid-demo .modal-btn {
  width: 140px;
  height: 70px;
  padding: 10px;
  border-radius: 15px;
  color: #333;
  border: 2px solid #999;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  box-sizing: border-box;
  display: block;
}

img {
  max-width: 100%;
}

p {
  line-height: 1.5;
}

.bold {
  font-weight: bold;
}

.underline {
  text-decoration: underline;
}

.uid-summary {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  height: 260px;
  margin-top: 40px;
}

.uid-codeblock {
  padding: 2px 3px;
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.1);
}

