.uid-datatable {
  width: 100%;
  border-top: 1px solid rgba(0, 0, 0, 0.5);
}
.uid-datatable table {
  width: 100%;
}
.uid-datatable.prompt h3,
.uid-datatable.prompt p {
  font-family: "Prompt";
}
.uid-datatable + .uid-datatable {
  margin-top: 40px;
}

.uid-datatable th,
.uid-datatable thead {
  background: rgb(243, 243, 243);
  font-weight: 500;
}

.uid-datatable th,
.uid-datatable td {
  padding: 15px 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.uid-datatable th:first-child,
.uid-datatable td:first-child {
  border-left: none;
}

.uid-datatable thead th {
  border-bottom-width: 2px;
}

.screen-capture {
  margin: 10px 0 20px;
}

.screen-capture figcaption {
  margin-top: 10px;
  text-align: center;
}

/* CodeMirror */
.CodeMirror {
  position: relative;
  width: auto;
  height: auto;
  padding: 10px;
  margin-bottom: 20px;
  background: rgb(243, 244, 245) !important;
}

.CodeMirror-scroll {
  width: 100%;
  margin-right: 0;
}

.CodeMirror-sizer {
  min-width: auto !important;
}

textarea.code_content:before {
  content: "ddd";
  position: absolute;
  display: block;
}

.CodeMirror pre.CodeMirror-line {
  width: 100%;
  font-size: 14px !important;
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace !important;
  white-space: pre-wrap;
  word-break: break-word;
}

.cm-tag {
  color: #c92c2c;
}

.cm-attribute {
  color: #2f9c0a !important;
}

.cm-s-default .cm-string {
  color: #1990b8 !important;
}

.cm-property {
  color: #905;
}

.cm-s-default .cm-qualifier {
  color: #690;
}

.cm-s-default .cm-comment {
  color: #647281;
}

