/* Scrollbar Style */
/* Firefox */
.scroll {
  scrollbar-width: auto;
  scrollbar-color: #767676 #ffffff;
}

/* Chrome, Edge, and Safari */
.scroll::-webkit-scrollbar {
  width: 16px;
}

.scroll::-webkit-scrollbar-track {
  background: #ffffff;
}

.scroll::-webkit-scrollbar-thumb {
  background-color: #767676;
  border-radius: 10px;
  border: 3px solid #ffffff;
}

.float-container {
  overflow: auto;
}

.float-group.before {
  border: 1px solid red;
}

.float-group.after {
  overflow: auto;
  border: 1px solid red;
}

.float-group.flow {
  display: flow-root;
  border: 1px solid red;
}

.float-group .float-item {
  float: left;
  border: 3px solid green;
}

/* :placeholder-shown styling */
.from-warchief {
