.code-wrap {
  white-space: nowrap;
  font-size: 0;
}
.code-wrap .code_preview iframe {
  width: 100%;
  border: none;
}
.code-wrap .code_preview iframe body {
  font-size: 16px;
}
.code-wrap .code-tab {
  padding-top: 10px;
  padding-left: 10px;
  display: flex;
  background-color: rgb(243, 244, 245);
}
.code-wrap .code-tab .code-btn {
  padding: 10px 20px;
  display: inline-block;
  border-radius: 15px;
  transition: all 0.5s;
}
.code-wrap .code-tab .code-btn.is-active {
  padding: 10px 20px;
  color: red;
  background-color: #fff;
}
.code-wrap .code-tab .code-btn.is-active .tab-label {
  display: block;
}
.code-wrap .code-tab .code-btn .tab-label {
  font-weight: bold;
  font-size: 16px;
  transition: all 0.1s;
}
.code-wrap .code-group {
  position: relative;
  width: 100%;
  vertical-align: top;
  font-size: 14px;
}
.code-wrap .code-group.is-active {
  left: 0;
}
.code-wrap .code-group.is-active .CodeMirror-measure {
  height: auto;
}